import React from 'react';
import { useHistory } from 'react-router-dom';
import { useEffect, useState } from 'react/cjs/react.development';
import ApiService from './API/ApiService';
import './EpisodeCard.css';

const EpisodeCard = ({ address }) => {
	const router = useHistory();
	const [episode, setEpisode] = useState({})

	async function fetchEpisode() {
		const response = await ApiService.getDataByFullAdress(address);
		setEpisode(response.data);
	}

	useEffect(() => {
		fetchEpisode();
	}, [])

	return (
		<div className="episode__card card-ep">

			<div className="card-ep__number">
				{episode.id}
			</div>

			<div className="card-ep__info">
				<div className="card-ep__name" onClick={() => router.push(`/episode/${episode.id}`)}>
					{episode.name}
				</div>
				<div className="card-ep__definition">
					{episode.episode}
				</div>
			</div>

			<div className="card-ep__date">
				{episode.air_date}
			</div>
		</div>
	);
};

export default EpisodeCard;