import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import ApiService from '../components/API/ApiService';
import CharacterCard from '../components/CharacterCard';
import EpisodeCard from '../components/EpisodeCard';
import Loader from '../components/UI/Loader/Loader';
import TransitionElement from '../components/UI/TransitionElement/TransitionElement';
import { useFetching } from '../hooks/useFetching';
import '../styles/CharacterId.css';

const CharacterId = () => {
	const router = useHistory();
	const params = useParams();

	const [character, setCharacter] = useState({});
	const [episode, setEpisode] = useState([]);

	const [fetchCharacterById, isLoading, error] = useFetching(async () => {
		const response = await ApiService.getCharacterById(params.id);
		setCharacter(response.data);
		setEpisode(response.data.episode);
	})

	useEffect(() => {
		fetchCharacterById();
	}, [])

	return (
		<TransitionElement>
			<div className="character-id">
				{isLoading
					? <Loader />
					: <div>
						<CharacterCard
							props={character}
							router={router}
						/>

						{episode.map(ep => 
							<EpisodeCard 
								key={ep}
								address={ep}
							/>
						)}
					</div>
				}
			</div>
		</TransitionElement>
	);
};

export default CharacterId;